* {
  box-sizing: border-box;
  outline: none !important;
  -webkit-tap-highlight-color: transparent;
  font-feature-settings: "pnum" on, "lnum" on;
}

img {
  max-width: 100%;
}

html {
  font-size: 10px;
  line-height: 122%;
}

body {
  position: relative;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  overflow-x: hidden;
  background-color: #F5F5F5;
}

.main {
  width: 100vw;
  overflow: hidden;
}

.lock {
  overflow: hidden;
  height: 100vh;
}

.section-title {
  font-weight: 600;
  font-size: 4.8rem;
  line-height: 107%;
  color: #390984;
  text-align: center;
}
@media all and (max-width: 1024px) {
  .section-title {
    font-size: 44px;
  }
}
@media all and (max-width: 768px) {
  .section-title {
    font-size: 40px;
  }
}
@media all and (max-width: 420px) {
  .section-title {
    font-size: 36px;
  }
}

.section-subtitle {
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 159%;
  text-align: center;
  color: #333333;
}
@media all and (max-width: 1024px) {
  .section-subtitle {
    font-size: 16px;
  }
}
@media all and (max-width: 768px) {
  .section-subtitle {
